@import "~@blueprintjs/core/lib/css/blueprint.css";

/* Font style overriding */
@import "https://fonts.googleapis.com/css?family=Krub:500:500|Ubuntu+Mono";
body { font-family: 'Krub', sans-serif;}
.bp3-running-text pre, .bp3-code-block,
.bp3-running-text code, .bp3-code,
pre, xmp, plaintext, listing {
    font-family: 'Ubuntu Mono', monospace;
}

/* ! Font style overriding */

html {
    /*url("https://imgs.xkcd.com/comics/optimization.png") no-repeat*/
    height: 120%;
    background: radial-gradient(ellipse at top center, rgba(103, 138, 124,0.90) 0%, rgba(72, 111, 95,0.90) 41%, rgba(28, 67, 51,1) 100%) no-repeat fixed
                , url("./assets/bg2.jpg") fixed no-repeat rgba(28, 67, 51,0.90);
}

@media only screen and (max-width: 450px) {
    html {
        background: radial-gradient(ellipse at top center, rgba(103, 138, 124,0.90) 0%, rgba(72, 111, 95,0.90) 41%, rgba(28, 67, 51,1) 100%) no-repeat fixed;
    }
}

body {
    min-height: 10%;
    box-sizing: border-box;
    margin: 0;
}

div.App {
    width: 90%;
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    min-height: 100%;
}