.timeline .gallery {
  position: relative;
  overflow: hidden; }
  .timeline .gallery .previous,
  .timeline .gallery .next {
    background: rgba(255, 255, 255, 0.35);
    width: 5em;
    height: 5em;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    content: " ";
    padding: 0.25em;
    z-index: 2;
    cursor: pointer;
    transition: all 150ms ease;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 5px; }
    .timeline .gallery .previous:hover,
    .timeline .gallery .next:hover {
      background: rgba(255, 255, 255, 0.65); }
    .timeline .gallery .previous::before,
    .timeline .gallery .next::before {
      width: 0;
      height: 0;
      content: " ";
      border: 8px solid transparent;
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%); }
  .timeline .gallery .previous {
    left: 0; }
    .timeline .gallery .previous.disabled {
      left: -5em; }
    .timeline .gallery .previous::before {
      border-right-color: #AAA;
      margin-left: calc((-1 * 8px) / 2); }
  .timeline .gallery .next {
    right: 0; }
    .timeline .gallery .next.disabled {
      right: -5em; }
    .timeline .gallery .next::before {
      border-left-color: #AAA;
      margin-left: calc(8px / 2); }
  .timeline .gallery .scroller {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    position: relative;
    transition: left 500ms ease-in; }
  .timeline .gallery .activity {
    flex: 1 0 100%;
    width: 100%;
    max-width: 100%; }
    .timeline .gallery .activity .container {
      background: #fff;
      border-radius: 0.15em;
      box-shadow: 0 1em 3em rgba(0, 0, 0, 0.35);
      color: #666;
      display: flex;
      flex-direction: row; }
      @media screen and (max-width: 700px) {
        .timeline .gallery .activity .container {
          display: block; } }
    .timeline .gallery .activity .image {
      flex: 0 0 40%; }
      .timeline .gallery .activity .image img {
        object-fit: cover;
        object-position: center center;
        width: 100%;
        height: 100%; }
    .timeline .gallery .activity .card {
      padding: 2em 1.5em;
      position: relative; }
    .timeline .gallery .activity h5 {
      color: #df7100; }
    .timeline .gallery .activity p {
      white-space: pre-line; }
    .timeline .gallery .activity .external-link {
      font-size: 1em;
      color: #aaa; }
      .timeline .gallery .activity .external-link svg.icon {
        margin-right: 0.5em; }
        .timeline .gallery .activity .external-link svg.icon use {
          fill: #aaa; }
      .timeline .gallery .activity .external-link:hover {
        color: #4488b4; }
        .timeline .gallery .activity .external-link:hover svg use {
          fill: #4488b4; }

