.vertical-timeline {
  width: 95%;
  margin: 2em auto;
  position: relative;
  /*padding: 2em 0;*/ }
  .vertical-timeline::after {
    /* clearfix */
    content: '';
    display: table;
    clear: both; }
  .vertical-timeline::before {
    /* this is the vertical line */
    content: '';
    position: absolute;
    top: 0;
    left: 18px;
    height: 100%;
    width: 4px;
    background: rgba(138, 155, 168, 0.2); }
  .vertical-timeline .vertical-timeline-element .vertical-timeline-element-thumbnail {
    max-height: 200px;
    display: block;
    max-width: 100%;
    margin: 0 auto 15px auto; }

@media only screen and (min-width: 800px) {
  .vertical-timeline::before {
    left: 50%;
    margin-left: -2px; }
  .vertical-timeline .vertical-timeline-element {
    width: 50%;
    clear: both;
    position: relative; }
    .vertical-timeline .vertical-timeline-element::before {
      content: "";
      width: 32px;
      height: 32px;
      position: absolute;
      /*border: 4px solid $timeline-color;*/
      background: rgba(138, 155, 168, 0.2);
      top: 50%;
      left: -16px;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      border-radius: 100%; }
    .vertical-timeline .vertical-timeline-element .vertical-timeline-element-content-techs {
      position: absolute;
      width: 100%; }
      .vertical-timeline .vertical-timeline-element .vertical-timeline-element-content-techs img {
        height: 32px;
        vertical-align: middle; }
    .vertical-timeline .vertical-timeline-element:nth-child(odd) {
      /* <- */
      float: left;
      padding-right: 32px; }
      .vertical-timeline .vertical-timeline-element:nth-child(odd)::before {
        right: -16px;
        left: auto; }
      .vertical-timeline .vertical-timeline-element:nth-child(odd) .vertical-timeline-element-icon {
        right: -8px;
        left: auto; }
      .vertical-timeline .vertical-timeline-element:nth-child(odd) .vertical-timeline-element-content-techs {
        top: 50%;
        right: 0;
        text-align: left;
        -webkit-transform: translate(100%, -50%) translateX(32px);
                transform: translate(100%, -50%) translateX(32px); }
    .vertical-timeline .vertical-timeline-element:nth-child(even) {
      /* -> */
      float: right;
      padding-left: 32px; }
      .vertical-timeline .vertical-timeline-element:nth-child(even) .vertical-timeline-element-content-techs {
        top: 50%;
        left: 0;
        text-align: right;
        -webkit-transform: translate(-100%, -50%) translateX(-32px);
                transform: translate(-100%, -50%) translateX(-32px); }
    .vertical-timeline .vertical-timeline-element .vertical-timeline-element-icon {
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-8px);
              transform: translateY(-8px);
      left: -8px; } }

@media only screen and (max-width: 799px) {
  .vertical-timeline::before {
    content: none; }
  .vertical-timeline .vertical-timeline-element {
    padding-top: 32px;
    clear: both;
    position: relative; }
    .vertical-timeline .vertical-timeline-element::before {
      content: "";
