    .vertical-timeline .vertical-timeline-element .vertical-timeline-element-content-techs:not(:empty) {
      width: 100%;
      margin-top: 0px; }
      .vertical-timeline .vertical-timeline-element .vertical-timeline-element-content-techs:not(:empty) img {
        height: 32px;
        vertical-align: middle; } }

@media only screen and (max-width: 9px) {
  .vertical-timeline::before {
    left: 0%;
    margin-left: -2px; }
  .vertical-timeline .vertical-timeline-element {
    margin-left: 20px;
