$timeline-color:rgba(138, 155, 168, 0.2);
$timeline-circle-size:32px;
$timeline-circle-padding: ($timeline-circle-size * 1 );

.vertical-timeline {
  width: 95%;
  margin: 2em auto;
  position: relative;
  /*padding: 2em 0;*/
  
  &::after {
    /* clearfix */
    content: '';
    display: table;
    clear: both;
  }

  &::before {
    /* this is the vertical line */
    content: '';
    position: absolute;
    top: 0;
    left: 18px;
    height: 100%;
    width: 4px;
    background: $timeline-color;
  }

  .vertical-timeline-element {
    .vertical-timeline-element-thumbnail {
      max-height: 200px;
      display: block;
      max-width: 100%;
      margin: 0 auto 15px auto;
    }
  }

}

@media only screen and (min-width: 800px) {
  
  .vertical-timeline {
    &::before {
      left: 50%;
      margin-left: -2px;
    }
    .vertical-timeline-element {
      width: 50%;
      clear: both;
      position: relative;
      
      &::before {
        content:"";
        width: $timeline-circle-size;
        height: $timeline-circle-size;
        position: absolute;
        /*border: 4px solid $timeline-color;*/
        background: $timeline-color;
        top: 50%;
        left: (-$timeline-circle-size * 0.5);
        transform: translateY(-50%);
        border-radius: 100%;
      }


      .vertical-timeline-element-content-techs {
        position: absolute;
        width: 100%;
        img {
          height: 32px;
          vertical-align: middle;
        }
      }

      &:nth-child(odd) {
        /* <- */ 
        float: left;
        padding-right: $timeline-circle-padding;

        &::before {
          right:(-$timeline-circle-size * 0.5);
          left: auto;
        }

        .vertical-timeline-element-icon {
          right: -8px;
          left: auto;
        }

        .vertical-timeline-element-content-techs {
          top: 50%;
          right: 0;
          text-align: left;
          transform: translate(+100%,-50%) translateX($timeline-circle-size );
        }
      
      }

      &:nth-child(even) {
        /* -> */
        float: right;
        padding-left: $timeline-circle-padding;

        .vertical-timeline-element-content-techs {
          top: 50%;
          left: 0;
          text-align: right;
          transform: translate(-100%,-50%) translateX(-$timeline-circle-size );
        }

      }
      
      .vertical-timeline-element-icon {
        position: absolute;
        top: 50%;
        transform: translateY(-8px);
        left: -8px;
      }
    }
  }
}

@media only screen and (max-width: 799px) {
  .vertical-timeline {
    &::before {
      content:none;
    }

    .vertical-timeline-element {
      padding-top: $timeline-circle-size;
      clear: both;
      position: relative;
      
      &::before {
        content:"";
        width: $timeline-circle-size;
        height: $timeline-circle-size;
        position: absolute;
        /*border: 4px solid $timeline-color;*/
        background: $timeline-color;
        left: 50%;
        top: 4px;
        transform: translateX(-50%);
        border-radius: 100%;
      }

      &::after {
        content: "";
        background: $timeline-color;
        position: absolute;
        top:4px + 16px - 2px;
        width: 100%;
        height: 4px;
      }

      .vertical-timeline-element-icon {
        position: absolute;
        left: 50%;
        transform: translate(-50%,-$timeline-circle-size + 4px) translateY(-50%);
      }

      .vertical-timeline-element-content-techs:not(:empty) {
        width: 100%;
        margin-top: 0px;
        
        img {
          height: 32px;
          vertical-align: middle;
        }
      }

    }

  }
}

@media only screen and (max-width: 9px) {
  .vertical-timeline {
    &::before {
      left: 0%;
      margin-left: -2px;
    }
    
    .vertical-timeline-element {
      margin-left: 4px + $timeline-circle-size * 0.5;
      clear: both;
      position: relative;
      
      &::before {
        content:"";
        width: $timeline-circle-size;
        height: $timeline-circle-size;
        position: absolute;
        /*border: 4px solid $timeline-color;*/
        background: $timeline-color;
        top: 50%;
        left: (-$timeline-circle-size) - 4px;
        transform: translateY(-50%);
        border-radius: 100%;
      }

      .vertical-timeline-element-icon {
        position: absolute;
        top: 50%;
        transform: translate(-50%,-8px);
        left: (-$timeline-circle-size * 0.5) - 4px;
      }

      .vertical-timeline-element-content-techs:not(:empty) {
        width: 100%;
        margin-top: 0px;
        
        img {
          height: 32px;
          vertical-align: middle;
        }
      }
    }
  }
}